.heading {
  background-color: green;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  margin-bottom: 70px;
  padding: 420px 0 80px;

  &--inner {
    padding-bottom: 40px;
  }

  &__container {
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
  }

  &__title {
    font-family: $helvetica-thin;
    font-size: $header-page-name-size;
    line-height: $line-height;
    color: #fff;
    text-transform: uppercase;
    font-weight: normal;
  }

  &__promo {
    font-family: $helvetica-light;
    font-size: $header-promo-size;
    width: 75%;
    margin: 0;

    &--title {
      margin-bottom: 30px;
    }
  }

  &__breadcrumbs {
    font-family: $roboto-light;
    font-size: 16px;
  }
}
