.contact {

  &__container {
    @include container;
  }

  & form {
    display: flex;
    flex-direction: column;
    width: 555px;
    font-family: $helvetica-light;
    font-size: 16px;
    line-height: 30px;
  }

  & input {
    border: 1px solid $links-color;
    padding: 10px 20px;
    margin-bottom: 25px;
    outline: none;

    &[type="text"] {
      font-family: $helvetica-light;
      font-size: 16px;
      line-height: 30px;
      color: $text-color;
    }

    &[type="submit"] {
      font-family: $helvetica-light;
      font-size: 16px;
      line-height: 30px;
      background-color: $links-color;
      color: #fff;
      width: 165px;
      border-radius: 25px;
      cursor: pointer;
    }
  }

  & select {
    font-family: $helvetica-light;
    font-size: 16px;
    line-height: 30px;
    border: 1px solid $links-color;
    color: $text-color;
    padding: 10px 20px;
    outline: none;
    margin-bottom: 25px;
  }

  & textarea {
    font-family: $helvetica-light;
    font-size: 16px;
    line-height: 30px;
    border: 1px solid $links-color;
    color: $text-color;
    padding: 10px 20px;
    outline: none;
    margin-bottom: 25px;
  }
}
