.slider-section {
  background-color: #fafafa;
  padding: 40px 0;
  margin-bottom: 25px;

  &--noslider {
    background-color: initial;
  }

  &__container {
    @include container;
  }

  &__title {
    @include section-title;
  }

  &__text {
    @include text;
  }

  &__list {
    @include list;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 30px 0;
  }

  &__item {
    display: flex;
    flex-direction: column;
    width: 99%;
    background-color: $overlay-color;
    padding: 40px 0;
    margin: auto;
    height: 100%;

    &--green {
      box-shadow: inset 0 0 0 1px $links-color;
      padding-bottom: 0;
    }

    &--noslider {
      width: 32%;
      margin-bottom: 20px;
      padding-bottom: 0;
    }

    &--no-overlay {
      background-color: initial;
      height: 300px;
    }

    &-title {
      font-family: $helvetica-light;
      font-size: $h3-size;
      font-weight: normal;
      text-align: center;
      color: $links-color;

      &--noslider {
        color: $title-color;
      }

      &--partners {
        font-family: $roboto-medium;
        font-size: 18px;
        color: $title-color;
      }
    }

    &-description {
      width: 80%;
      margin: 0 auto;
      font-family: $roboto-light;
      font-size: 14px;
      color: $text-color;
      text-align: center;
    }
  }

  &__img {
    display: flex;
    width: 70%;
    height: 150px;
    margin: auto;

    & img {
      height: 100%;
      width: auto;
      margin: 0 auto;
    }

    &--normal {
      min-height: 150px;
      flex: 150px;

      & img {
        @include img;
        margin: auto;
      }
    }
  }
}
