.team {

  &__container {
    @include container;
  }

  &__list {
    @include list;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__item {
    width: 360px;
    margin-bottom: 35px;
  }

  &__img {

    & img {
      @include img;
    }
  }

  &__name {
    font-family: $roboto-medium;
    font-size: 24px;
    margin-bottom: 0;
  }

  &__spec {
    font-family: $roboto-light;
    font-size: 19px;
    margin-top: 0;
  }

}
