.cards {
  margin-bottom: 65px;

  &__container {
    @include container;
  }

  &__list {
    @include list;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__item {
    display: flex;
    flex-direction: column;
    width: 32%;
    background-color: $overlay-color;
    padding-top: 25px;
  }

  &__img {
    display: flex;
    height: 160px;

    & img {
      height: 100%;
      width: auto;
      margin: 0 auto;
    }
  }

  &__title {
    font-family: $helvetica-light;
    font-size: $h3-size;
    font-weight: normal;
    text-align: center;
  }
}
