@font-face {
  font-family: "HelveticaNeueLT-Thin";
  src: url("../fonts/HELVETICALT35THIN.eot"); /* IE9 Compat Modes */
  src: url("../fonts/HELVETICALT35THIN.eot?#iefix") format("embedded-opentype"), /* IE6-IE8 */
    url("../fonts/HELVETICALT35THIN.otf") format("opentype"), /* Open Type Font */
    url("../fonts/HELVETICALT35THIN.svg") format("svg"), /* Legacy iOS */
    url("../fonts/HELVETICALT35THIN.ttf") format("truetype"), /* Safari, Android, iOS */
    url("../fonts/HELVETICALT35THIN.woff") format("woff"), /* Modern Browsers */
    url("../fonts/HELVETICALT35THIN.woff2") format("woff2"); /* Modern Browsers */
  font-weight: normal;
  font-style: normal;
}

