.content-section {
  margin-bottom: 65px;

  &__container {
    @include container;
  }

  &__title {
    @include section-title;
  }

  &__flex-container {
    @include flex-container;

    &--reverse {
      flex-direction: row-reverse;
    }
  }

  &__media {
    @include half;
    height: 255px;

    & iframe {
      width: 100%;
      height: 255px;
    }

    & img {
      @include img;
    }
  }

  &__text {
    @include half;
    display: flex;
    flex-direction: column;
    font-family: $helvetica-thin;
    font-size: $text-size;
    color: $text-color;
    line-height: 1.5em;
    margin-top: -10px;

    &--nomargin {
      margin-top: 0;
    }

    &-default {
      margin-top: 0;

      &--forgotten-title-because-maria {
        margin-top: auto;
        margin-bottom: auto;
      }
    }

    &-bold {
      margin-top: 0;
      font-family: $helvetica-light;
      color: #000;
    }
  }

  &__link {
    margin-top: auto;
    margin-bottom: -8px;
  }
}
