.main-nav {

  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-content: center;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__item {
    margin-right: 50px;

    &:last-child {
      margin-right: 0;
    }
  }

  &__link {
    font-family: $helvetica-light;
    font-size: $header-menu-size;
    line-height: $line-height;
    color: #fff;
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }

    &:active {
      text-decoration: underline;
    }
  }
}
